    width: 30%;
    display: flex;
    
}

.cartwidget-product__action {
    
    display: flex;
    align-self: center;
}

.cart-product__details {
    display: flex; 
    flex-direction: row;
    flex-wrap: nowrap;
    padding: 1%;
    align-self: center;
}

.container__btn{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

button.btn-delete-all {
    margin-right: 10px;
    margin-bottom: 10px;
    margin-top: 10px;
    background: black;
    cursor: pointer;
    border: 1px solid black;
    color: white;
    font-size: 16px;
    text-transform: uppercase;
    padding: 2px 2px;
    transition: all 0.3s ease;
}

button.btn-delete-all:hover {
    background: white;
    color: black;
}

.dropdown-toggle::after {
    display:none !important;
}

.carritoIcon{
    color: black;
}

.carritoIcon:hover{
    cursor: pointer;
}
.cart-product__buscar{
    align-self: center;
    justify-self: center;
}

.item-cart-search {
    /* float: left; */
    display: flex;
    align-items: center;
    align-content: center;
    justify-items: center;
    justify-content: center;
    justify-self: center;
    width: 150px;
    border-bottom: 1px solid #ccc;
    
}

.datopedido__h2{
    margin-top: 50px;
}
.list-products {
    float: left;
    width: 100%;
    margin-bottom: 20px;
}

.modal-custom {
    position: fixed;
    z-index: 1;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: white;
    width: 90vw;
    max-height: calc(100vh - 210px);
    overflow-y: auto;
    box-shadow: 1px 5px 5px #00000047;
    border-radius: 5px;
    padding: 20px;
    
    
    
  
}
.modal-custom svg {
    position: absolute;
    right: 10px;
    top: 40px;
    cursor: pointer;
}

.btncerrar{
    float: right;
}

.btncerrar:hover{
    background-color: black;
    color: white;
    cursor:pointer;
}

.modal-custom.modalBlur > *:not(.modal) {
    -webkit-filter: blur(5px);
            filter: blur(5px);
  }

