* {
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -webkit-text-shadow: rgba(0, 0, 0, .01) 0 0 1px;
    text-shadow: rgba(0, 0, 0, .01) 0 0 1px
}

body {
    font-family: 'Rubik', sans-serif;
    font-size: 14px;
    font-weight: 400;
    background: #E0E0E0;
    color: #000000
}

ul {
    list-style: none;
    margin-bottom: 0px
}

.button {
    display: inline-block;
    background: #0e8ce4;
    border-radius: 5px;
    height: 48px;
    transition: all 200ms ease
}

.button a {
    display: block;
    font-size: 18px;
    font-weight: 400;
    line-height: 48px;
    color: #FFFFFF;
    padding-left: 35px;
    padding-right: 35px
}

.button:hover {
    opacity: 0.8
}

.cart_section {
    width: 100%;
    padding-top: 93px;
    padding-bottom: 111px
}

.cart_title {
    font-size: 30px;
    font-weight: 500
}

.cart_items {
    margin-top: 8px
}

.cart_list {
    border: solid 1px #e8e8e8;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1);
    background-color: #fff
}

.cart_item {
    width: 100%;
    height: 100%;
    padding: 15px;
    padding-right: 46px
}

.cart_item_image {
    width: 133px;
    height: 133px;
    float: left;
    -webkit-transform: translateY(35px);
            transform: translateY(35px);
    align-items: center;
    align-content: center;
    justify-content: center;
    justify-items: center;
    justify-self: center
}
.cart_item_image img {
    max-width: 80%;
    align-self: center;

}


.cart_item_info {
    width: calc(100% - 133px);
    float:inline-end;
    padding-top: 18px
}

.cart_item_name {
    margin-left: 7.53%
}

.cart_info_col{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-items: center;
}

.cart_item_title {
    font-size: 14px;
    font-weight: 400;
    text-align: start;
    color: rgba(0, 0, 0, 0.5)
}

.cart_item_text {
    
    font-size: 18px;
    margin-top: 35px;
    text-align: start;
}

.cart_item_text span {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 11px;
    -webkit-transform: translateY(4px);
    transform: translateY(4px)
}

.cart_item_price {
    text-align: right
}

.cart_item_total {
    text-align: right
}

.order_total {
    width: 100%;
    height: 60px;
    margin-top: 30px;
    border: solid 1px #e8e8e8;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1);
    padding-right: 46px;
    padding-left: 15px;
    background-color: #fff
}

.order_total_title {
    display: inline-block;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.5);
    line-height: 60px
}

.order_total_amount {
    display: inline-block;
    font-size: 18px;
    font-weight: 500;
    margin-left: 26px;
    line-height: 60px
}

.cart_buttons {
    margin-top: 60px;
    text-align: right
}

.cart_button_clear {
    display: inline-block;
    border: none;
    font-size: 18px;
    font-weight: 400;
    line-height: 48px;
    color: rgba(0, 0, 0, 0.5);
    background: #FFFFFF;
    border: solid 1px #b2b2b2;
    padding-left: 35px;
    padding-right: 35px;
    outline: none;
    cursor: pointer;
    margin-right: 26px
}

.cart_button_clear:hover {
    border-color: #0e8ce4;
    color: #0e8ce4
}

.cart_button_checkout {
    display: inline-block;
    border: none;
    font-size: 18px;
    font-weight: 400;
    line-height: 48px;
    color: #FFFFFF;
    padding-left: 35px;
    padding-right: 35px;
    outline: none;
    cursor: pointer;
    vertical-align: top
}

.cart-product__action {
    
    display: flex;
    align-self: flex-end;
}

.form{
    display: flex;
    flex-direction: column;
    align-items: center;

}

.form input{
    margin: 20px;
    padding: 5px;
}

.form__text{
    width: 50%;
}

.form__number{
    width: 30%;
}

.form__email{
    width: 60%;
}
.form button{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    align-self: center;
    background: black;
    cursor: pointer;
    border: 1px solid black;
    color: white;
    font-size: 16px;
    text-transform: uppercase;
    padding: 2px 2px;
    transition: all 0.3s ease;
}

.error__titulo{
    color: red;
    font-weight: bold;
}

.error__descripcion{
    font-style: italic ;
}
